import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import bridge from '@vkontakte/vk-bridge';
import party from '../img/party.png';
import './Games.css';



const fetchedSuccess = {
	0: "photo-194171844_457239023",
	20: "photo-194171844_457239018",
	40: "photo-194171844_457239024",
	60: "photo-194171844_457239020",
	80: "photo-194171844_457239021",
	100: "photo-194171844_457239022"
};

function share(e,success){
	e.preventDefault();
	let app = "Приложение: https://vk.com/app7401331_154621764";
	bridge.send("VKWebAppShowWallPostBox", { "message": app , "attachments": fetchedSuccess[success] })


	console.log(success);
}


const Rezult = props => (
	<Panel id={props.id}>
		<div class="block-rezult">
			<h1 class="text-friends sensei">ВЫ ЗНАЕТЕ<br/> СВОИХ ДРУЗЕЙ <br/> НА <span class="percent">{props.success}%</span> </h1>
			<button class="share" onClick={(e) =>share(e,props.success)}>Поделиться</button>
			<button class="again" onClick={props.go} data-to="home" >Сыграть еще</button>
			<img className="party" src={party} alt="друзья" />
		</div>
	</Panel>
);

Rezult.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Rezult;
