import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Gallery, Group, ModalCard } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import './Games.css';
import axios from 'axios-jsonp-pro';
import sleep from '@react-corekit/sleep';
import FalseImg from '../img/false.png';
import Wait from '../img/wait.gif';
import TrueImg from '../img/true.png';



const osName = platform();




const Games = props => {

	const [fetchedGroups, setGroups] = useState([]);
	const [fetchedGames, setGames] = useState([]);
	const [answer, setAnswer] = useState([]);
	const [balls, setBalls] = useState(0);
	const [slideIndex, setslideIndex] = useState(0);
	const [itogGames, setItogGames] = useState(0);


	useEffect(() => {

		async function fetchData() {

			const token = await bridge.send("VKWebAppGetAuthToken", { "app_id": 7401331, "scope": "friends,video, groups" });
			let urlFriends = 'https://api.vk.com/method/friends.get?user_ids=' +
				props.fetchedUser.user_id +
				'&access_token=' + props.fetchedToken +
				'&order=random' +
				'&fields=name,last_name,photo_100' +
				'&count=30' +
				'&v=5.103';

			let gameStage = [];
			let friends = await axios.jsonp(urlFriends);

			let allFriends = friends.response.items;
			let massivFriends = [];

			// удаляем друзей, у которых нет фоток и кто не заблокирован.
			allFriends.forEach(function (item, ) {

				if (item.deactivated || item.photo_100.toString().indexOf("camera_100") != -1) { }
				else massivFriends.push(item);

			});

			let user_ids = '';
			massivFriends.forEach(function (item, ) {
				user_ids += item.id + ",";

			});
			console.log(massivFriends);
			for (const friend of massivFriends) {

				if (gameStage.length == 5) break;

				let useFriends = false;
				for (const game of gameStage) {
					if (game.user.id == friend.id || game.friend.id == friend.id) {
						useFriends = true;
					}
				}

				if (useFriends) continue;

				let urlGroup = 'https://api.vk.com/method/groups.get?user_id=' +
					friend.id +
					'&access_token=' + props.fetchedToken +
					'&extended=1' +
					'&fiter=publics,group' +
					'&count=15' +
					'&v=5.103';

				let groups = await axios.jsonp(urlGroup);
				if (groups.response) {

					let massivGroups = groups.response.items.sort(function () {
						return Math.random() - 0.5;
					});

					for (const group of massivGroups) {

						if (group.is_closed == 1) {
							continue;
						}

						let friendsToGame = null;
						await sleep(1000);
						let urlCheckGroups = 'https://api.vk.com/method/groups.isMember?user_ids=' +
							user_ids +
							'&group_id=' + group.id +
							'&access_token=' + props.fetchedToken +
							'&v=5.103';

						let isMembers = await axios.jsonp(urlCheckGroups);
						let resultMembers = [];
						if (isMembers.response) {
							resultMembers = isMembers.response.filter(function (memberGroup) {
								return memberGroup.member == 0;
							});

							if (resultMembers.length > 0) {

								// чтобы на последнем этапе не попался тот же самый пользователь.
								let randomMembers = resultMembers.sort(function () {
									return Math.random() - 0.5;
								});
								let user = massivFriends.filter(function (memberGroup) {
									return memberGroup.id == resultMembers[0].user_id;
								});

								//moving - нужен для рандомного расположения элементов.
								gameStage.push(
									{ 'user': user[0], 'group': group, 'friend': friend, "moving":Math.floor(Math.random() * 2) + 0 });
								fetchedGroups.push(group);
								break;
							}


						}

					}

				}

			}
			setGames(gameStage);
			setGroups(fetchedGroups);
		}
		fetchData();
	}, []);


	function CheckAnswer(id, i, e, go) {
		e.preventDefault();

		if(i - 1  >= answer.length)
		{
			setslideIndex(answer.length);
			return;
		}
		
		if (i < answer.length) 
		{
			return;
		}

		let thisTimeGames = answer.slice();
		let itogForPage = itogGames;
		if (id == fetchedGames[i].friend.id) {
			thisTimeGames.push({ "rezult": true, "id": id });
			setBalls(balls + 1);
			setItogGames(itogGames+20);
			itogForPage = itogForPage + 20;
		}
		else {
			thisTimeGames.push({ "rezult": false, "id": id });
		}
		setAnswer(thisTimeGames);


		if (i != fetchedGames.length - 1) 
		{
			setTimeout(
				function() {
					setslideIndex(slideIndex + 1);
				}
				.bind(this),
				1500
			);
		}
		else{
			let clone= cloneEvent(e);
			clone.currentTarget.dataset.itog = itogForPage;
			setTimeout(
				function() {
					go(clone);
				}
				.bind(this),
				1500
			);
		} 
	}

	function cloneEvent(e) {
		function ClonedEvent() {};
		let clone=new ClonedEvent();
		for (let p in e) {
			let d=Object.getOwnPropertyDescriptor(e, p);
			if (d && (!d.writable || !d.configurable || !d.enumerable || d.get || d.set)) {
				Object.defineProperty(clone, p, d);
			}
			else {
				clone[p] = e[p];
			}  
		}
		Object.setPrototypeOf(clone, e);
		return clone;
	}

	function getAnswer(i, id) {
		if (answer.length > i) {
			if (answer[i].rezult && answer[i].id == id) return TrueImg;
			else if (!answer[i].rezult && answer[i].id == id) return FalseImg;
			else return "";
		}
		else return "";
	}

	function finishGames(e, go) {
		e.preventDefault();
		if (answer.length >= fetchedGames.length) {
			go(e);
		}

	}


	const Round = props => {

		return (
			<div class="games_raund margin-top50">
				<div><h2  class="size40 sensei who-text ">Угадай кто</h2>
				<p class="opensans-regular  without-margin" >подписан на:</p></div>
				<div class="about_group opensans-regular">
					<img alt="friends" class="group_img" src={props.item.group.photo_200} />
					<p>{props.item.group.name}</p>
					<p>100 000 подписчиков</p>
				</div>

				<div></div>

				<div class="answer_stroka">
					<div class="button_answer" data-itog={itogGames} data-to="rezult"  onClick={(e) =>
						 CheckAnswer( props.item.moving == 1 ? props.item.friend.id : props.item.user.id, props.i, e, props.go)}>
						<div class="about_users">
							<img class="img_answer"
							src={getAnswer(props.i, props.item.moving == 1 ? props.item.friend.id : props.item.user.id)} />
							<img alt="friends" class="user_img" 
							src={props.item.moving == 1 ? props.item.friend.photo_100 : props.item.user.photo_100 } />
							<button  class="left-user" >{props.item.moving ==  1 ? props.item.friend.first_name : props.item.user.first_name}</button>
						</div>
					</div>

					<div class="button_answer" data-itog={itogGames} data-to="rezult" onClick={(e) =>
						 CheckAnswer(props.item.moving == 0 ? props.item.friend.id : props.item.user.id, props.i, e, props.go)}>
						<div class="about_users">
							<img class="img_answer" 
							src={getAnswer(props.i, props.item.moving == 0 ? props.item.friend.id : props.item.user.id)} />
							<img alt="friends" class="user_img" 
							src={props.item.moving ==  0 ? props.item.friend.photo_100 : props.item.user.photo_100} />
							<button class="right-user">{props.item.moving ==  0 ? props.item.friend.first_name : props.item.user.first_name}</button>
						</div>
					</div>
				</div>

				{/*props.item.friend.id === fetchedGames[fetchedGames.length - 1].friend.id &&
					<div class="about_users">
						<button data-to="rezult" onClick={(e) => finishGames(e, props.go)}>
							Закончить Игру
						</button>
					</div>
					
				*/}
			</div>
		)
	}

	const Ready = props => {

		return (
			<div class="wait">
				<img class="wait_img" src={Wait} />
				<h1>Ждем игру</h1>
			</div>
		)

	}

	return (
		<Panel id={props.id}>
			<Group>
				{fetchedGames.length == 0 &&
					<Ready />
				}
				<Gallery
					slideWidth="100%"
					style={{ height: "100%" }}
					className='okno'
					slideIndex={slideIndex}
					onChange={setslideIndex}
					bullets="dark"
				>

					{fetchedGames.length > 0 &&
						fetchedGames.map((item, i) => (
							<Round
								item={item}
								i={i}
								go={props.go}
							/>
						))
					}



				</Gallery>
			</Group>

		</Panel>)
};

Games.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Games;


